@import '../utils/variables.scss';

.gallery-carousel {
  display: flex;
  justify-content: center;
  align-items: center;

  &__container {
    display: flex;
    gap: 30px;
    max-width: 705px;
    min-width: 225px;
    width: 70vw;
    overflow: hidden;

    padding: 40px 20px 40px 0;
  }

  &__image {
    display: block;
    width: 225px;
    object-fit: contain;
    border-radius: 20px;

    transition: transform $initial-transition;
  }

  &__button {
    display: block;

    background-color: transparent;
    border: none;
    cursor: pointer;

  }

  &__icon {
    width: 20px;
    height: 60px;
    object-fit: cover;
  }
}